const Reader = require("./StringReader");

Array.prototype.sortByDescending = function (func) {
    for (let j=0;j<this.length; j++) {
        let fin = true;
        for (let i=1;i<this.length; i++) {
            let a = func(this[i-1]);
            let b = func(this[i]);
            if (a<b) {
                fin = false;
                let t = this[i];
                this[i] = this[i-1];
                this[i-1] = t;
            }
        }
        if (fin) {
            break;
        }
    }
    return this;
};
Array.prototype.minIndexBy = function(func) {
    let mx = func(this[0], 0, this);
    let index = 0;
    this.forEach((function (i, ix, items) {
        let x = func(i, ix, items);
        if (x<mx) {
            mx = x;
            index = ix;
        }
    }));
    return index;
};
Array.prototype.minBy = function (func) {
    return this[this.minIndexBy(func)];
}
Array.prototype.maxIndexBy = function(func) {
    let mx = func(this[0], 0, this);
    let index = 0;
    this.forEach((function (i, ix, items) {
        let x = func(i, ix, items);
        if (x>mx) {
            mx = x;
            index = ix;
        }
    }));
    return index;
};
Array.prototype.maxBy = function (func) {
    return this[this.maxIndexBy(func)];
}
var DefTokenTypes = {
    EOF: -1,
    NONE: 0,
    WHITESPACE: 1,
    SYMBOL: 2,
    LITERAL: 3,
    WORD: 4,
    str: function (i) {
        for (let k of Object.keys(this)) {
            if (this[k]===i) return k
        }
        return null;
    }
}

/**
 *
 * @param opt {{symbols:String[]?,
 * literals: {begin:String,end:String[]|String,name:String?,multiline:Boolean=true}[]?,
 * preScanners:[{scan,result}]?,
 * postScanners:[{scan,result}]?}}
 * @param code {String?}
 * @constructor
 */
function Tokenizer(opt, code) {
    opt = opt || {};
    code = code || "";
    var _ = this;
    _.reader = new Reader(code);
    _.continueWith = function (code) {
        _.reader = null;
        _.reader = new Reader(code);
    }
    _.reset = function (code) {
        _.continueWith(code);
        _.tokens = [];
    }
    _.postScanners = opt.postScanners || [];
    _.preScanners = opt.preScanners || [];
    _.syms = opt.symbols || [];
    _.lits = opt.literals || [];
    // console.log(opt, _.syms)
    _.lits.forEach(function (l) {
        _.syms.push(l.begin);
    });
    _.syms = _.syms.sortByDescending(function (it) {
        return it.length
    });
    _.isWhite = function (c) {
        return [' ', '\r', '\n', '\t'].indexOf(c)>=0;
    }
    _.maxSymLen = _.syms.maxBy(function (i) {
        return i.length
    }).length;
    // for (let x of _.syms) {
    //     if (x.length>_.maxSymLen) _.maxSymLen = x.length;
    // }
    _.skips = [];
    _.match = [];
    _.scanSymbol = function (sym) {
        var rd = _.reader;
        var step = 0;
        sym = sym || _.syms;
        _.skips = [];
        _.match = [];
        while (rd.avail() && step < _.maxSymLen) {
            let c = rd.readNext();
            for (let i = 0; i < sym.length; i++) {
                if (_.skips.indexOf(i)>=0) continue;
                if (sym[i][step] === c) {
                    if (sym[i].length - 1 === step) {
                        _.match.push(i);
                    }
                    continue;
                }
                _.skips.push(i);
            }


            if (sym.length - _.skips.length === 0) {
                if (_.match.length > 0) {
                    // let mml = _.match.min();
                    let mml = Math.min(_.match);
                    _.reader.pushBack(_.reader.readCount() - sym[mml].length);
                    return true
                }
                return false
            }
            step++;
        }
        if (sym.length - _.skips.length === 1) {
            return _.match.length>0;
        }
        return false
    }
    _.scanWhitespace = function () {
        let readWhite = false;
        while (_.reader.avail()) {
            let c = _.reader.readNext();
            if (_.isWhite(c)) {
                readWhite = true;
            } else {
                _.reader.pushBack(1);
                return readWhite
            }
        }
        return readWhite;
    }
    _.scanLiteral = function (lt) {
        let minSize = lt.end.minBy(function (it) {
            return it.length
        }).length || 0;

        while (_.reader.avail()) {
            let c = _.reader.readNext()
            if (_.reader.readCount() >= minSize) {
                for (let ei = 0; ei < lt.end.length; ei++) {
                    let e = lt.end[ei];
                    if (_.reader.getCurrentString().endsWith(e)) {
                        if (!_.reader.getCurrentString().endsWith(lt.escape + e))
                            return e
                    }
                }
            }
            if (!lt.multiline) {
                if (c === '\n') {
                    if (!_.reader.prevMatch(lt.escape)) {
                        if (_.reader.prevMatch('\r')) {
                            if (!_.reader.prevMatch(lt.escape)) {
                                _.reader.pushBack(2)
                                return false
                                // continue;
                            }
                        } else {
                            _.reader.pushBack()
                            return false
                        }
                    }
                }
            }

        }
        return lt.multiline
    }

    _.getToken = function (white = false, types) {
        while (true) {
            _.reader.mark();
            for (let preS = 0; preS < _.preScanners.length; preS++) {
                const preScanner = _.preScanners[preS];
                if (preScanner.scan) {
                    const st = preScanner.scan(_.reader);
                    if (st) {
                        if ((!types) || (types && types.indexOf(st)>=0))
                            return preScanner.result(_.reader);
                        else {
                            _.reader.mark();
                            break;
                        }
                    } else {
                        _.reader.rewind();
                    }
                }
            }
            if (_.scanWhitespace()) {
                if ((!types) || (types && types.indexOf(DefTokenTypes.WHITESPACE)>=0))
                    if (white) {
                        return {
                            index: _.reader.mrk,
                            type: DefTokenTypes.WHITESPACE,
                            text: _.reader.getCurrentString()
                        };
                    } else {
                        _.reader.mark();
                    }
                else continue
            } else {
                _.reader.rewind();
            }
            if (_.scanSymbol()) {
                let sym = _.reader.getCurrentString();
                let lit = _.lits.find(function (it) {
                    return it.begin === sym
                });

                if (lit) {
                    let __lt;
                    if ((__lt = _.scanLiteral(lit))) {
                        if ((!types) || (types && types.indexOf(DefTokenTypes.LITERAL)>=0))
                        {
                            return {
                                name: lit.name,
                                index: _.reader.mrk,
                                type: DefTokenTypes.LITERAL,
                                text: _.reader.getCurrentString(),
                                begin: lit.begin,
                                end: __lt === true ? "" : __lt,
                                getContent: function () {
                                    return this.text.slice(this.begin.length, this.text.length - this.end.length);
                                }
                            }
                        }else continue
                    } else {
                        _.reader.rewind();
                    }
                } else {
                    if ((!types) || (types && types.indexOf(DefTokenTypes.SYMBOL)>=0))
                        return {
                            index: _.reader.mrk,
                            type: DefTokenTypes.SYMBOL,
                            text: sym
                        }
                    else continue
                }
            } else {
                _.reader.rewind();
            }
            if (_.reader.avail()) {
                for (let postS = 0; postS < _.postScanners.length; postS++) {
                    let scanner = _.postScanners[postS];
                    if (scanner.scan) {
                        let s = scanner.scan(_.reader);
                        if (s) {
                            if ((!types) || (types && types.indexOf(s)>=0))
                                return scanner.result(_.reader);
                        } else {
                            _.reader.rewind();
                        }
                    }
                }
                let next = _.reader.readNext();
                if ((!types) || (types && types.indexOf(DefTokenTypes.NONE)>=0))
                    return {
                        index: _.reader.mrk,
                        type: DefTokenTypes.NONE,
                        text: next
                    };
                else {
                    continue;
                }
            } else {
                return {
                    index: -1,
                    type: DefTokenTypes.EOF,
                    text: null
                }
            }
        }
    }
    _.tokens = []
    _.getAlltokens = function (white, types) {
        let t;
        do {
            t = _.getToken(white, types);
            if (t.text != null) {
                _.tokens.push(t);
            }
        } while (t.text != null);
        return _.tokens
    }
}
module.exports = {Tokenizer: Tokenizer, TokenType: DefTokenTypes, StringReader: Reader};