function max2(a, b) {
    return a > b ? a : b
}

function min2(a, b) {
    return a > b ? b : a
}
let SR = function (str) {
    var _ = this;
    _.mrk = 0;
    _.pos = 0;
    _.marked = "";
    _.readCount = function () {
        return _.pos - _.mrk;
    }

    _.hasPrev = function () {
        return _.readCount() > 1;
    }

    _.prev = function () {
        return str.charAt(max2(_.pos - 2, _.mark));
    }

    _.avail = function () {
        return _.pos < str.length
    }

    _.mark = function (i) {
        i = i || _.pos;
        _.mrk = i;
        _.marked = "";
    }

    _.rewind = function () {
        _.marked = ""
        _.pos = min2(max2(_.mrk, 0), max2(_.pos, 0))
    }

    _.pushBack = function (n) {
        _.marked = _.marked.slice(0, max2(_.readCount() - n, 0));
        if (_.pos - n >= 0) _.pos -= n
    }

    _.prevMatch = function (c) {
        if (_.hasPrev()) {
            if (_.prev() === c) {
                return true
            }
        }
        return false
    }

    _.readNext = function () {
        _.pos += 1;
        if (str.length < _.pos) {
            return '\0';
        }
        _.marked += str[_.pos - 1];
        return str[_.pos - 1]
    }

    _.getCurrentString = function () {
        return _.marked
    }
}
module.exports = SR;